.container {
  width: 320px;
  margin: 0 auto;
  background-color: #000000;
}
.container--index {
  background-image: url("../img/fon.jpg");
  background-position: 0 0;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}
.header {
  display: none;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 10px;
    border: 1px dotted #403f3f;
  }
  .header__item {
    padding: 20px;
    border-right: 1px dotted #403f3f;
  }
  .header__item-a {
    display: block;
    margin-bottom: 15px;
    color: #f2f2f2;
  }
  .header__item--links {
    min-width: 150px;
    font-size: 21px;
    border: none;
  }
  .header__item--rus,
  .header__item--eng {
    min-width: 50px;
  }
  .header__item--rus a,
  .header__item--eng a {
    min-width: 75px;
    min-height: 75px;
  }
  .header__item--text {
    min-width: 250px;
    color: #d102f5;
    font-size: 30px;
  }
}
@media (min-width: 1200px) {
  .header__item--rus,
  .header__item--eng {
    min-width: 100px;
  }
  .header__item--rus a,
  .header__item--eng a {
    min-width: 100px;
    min-height: 100px;
  }
  .header__item--text {
    min-width: 550px;
  }
  .header__item--links {
    min-width: 235px;
  }
}
.main {
  padding-bottom: 50px;
}
.main__name {
  margin-top: 25px;
}
.main__table {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-left: 20px;
  padding-right: 20px;
}
.main__discord {
  margin-top: 25px;
  margin-bottom: 25px;
}
.main__title {
  color: #f3bc16;
  line-height: 30px;
}
.main__text {
  padding-right: 10px;
  padding-left: 10px;
  color: #90c7e0;
  text-align: center;
}
.main__text--terms {
  margin-left: 10px;
}
.main__list--planet {
  color: #f3bc16;
}
.main ol {
  width: 40%;
  margin: 0 auto;
}
.main__content {
  margin-bottom: 30px;
  margin-top: 20px;
  padding: 0;
  list-style-type: none;
}
.main__content li {
  padding-top: 10px;
}
.main__text--a {
  color: #ffeb3b;
}
.main__text--a:hover {
  color: #b1a32a;
}
.main__text--a:active {
  color: #756a0c;
}
@media (min-width: 768px) {
  .main__text {
    padding-right: 20px;
    padding-left: 20px;
    text-align: justify;
  }
  .main__text--support,
  .main__text--levels {
    text-align: center;
  }
  .main__table {
    padding-left: 100px;
    padding-right: 100px;
  }
  .main ol {
    width: 30%;
  }
}
@media (min-width: 1200px) {
  .main__text {
    padding-left: 100px;
    padding-right: 100px;
  }
  .main ol {
    width: 20%;
  }
}
.units {
  max-width: 500px;
}
.units__name {
  font-size: 26px;
  color: #d102f5;
}
.units--links {
  margin: 0 auto;
}
.list {
  padding: 10px 0;
  font-size: 21px;
  list-style-type: none;
}
.list__item {
  padding-top: 15px;
  padding-bottom: 15px;
  font-style: italic;
  text-decoration: underline;
}
.list__item a {
  color: #e6b5b5;
}
.list__item--mod {
  color: #96c0e2;
}
.list__item--calc {
  color: #2196f3;
}
.list__item a:hover {
  color: #b18b8b;
}
.list__item a:active {
  color: #6b3e3e;
}
.footer {
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: #211e1e;
}
.footer__text {
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
  font-weight: bold;
  font-size: 12px;
}
.extra-header {
  padding-top: 10px;
}
.extra-header__img {
  max-width: 100%;
}
.section__title {
  margin-top: 40px;
  padding-left: 10px;
  padding-right: 10px;
  color: #ffc107;
  font-size: 24px;
}
.section__text {
  padding-right: 20px;
  padding-left: 20px;
  color: #90c7e0;
  text-align: center;
}
.section__table {
  width: auto;
  height: auto;
  margin: 0 auto;
  font-size: 15px;
  text-align: right;
  vertical-align: middle;
}
.section__div {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  padding-right: 10px;
  padding-left: 10px;
}
.section__div h2 {
  margin-top: 15px;
}
.section__text--storm {
  margin-right: auto;
  margin-left: auto;
}
.section__img {
  max-width: 100%;
}
.section--white {
  margin-top: 50px;
}
@media (min-width: 768px) {
  .section__title {
    font-size: 28px;
  }
  .section__text {
    padding-right: 20px;
    padding-left: 20px;
    text-align: justify;
  }
  .section__table--time-mod {
    margin-right: 60px;
    margin-left: 60px;
  }
}
@media (min-width: 1200px) {
  .section__text {
    padding-left: 100px;
    padding-right: 100px;
  }
}
.table {
  width: 55%;
  margin: 15px auto;
}
.table--min {
  width: 20%;
}
.table__gray {
  background-color: #2b2a2a;
}
.table__gray--dark {
  text-align: center;
  background-color: #4a4949;
}
.table--extra {
  margin-bottom: 20px;
}
.build-footer {
  margin-top: 40px;
}
.build-footer__text {
  padding-left: 20px;
  padding-right: 20px;
  font-size: 15px;
  text-align: center;
}
@media (min-width: 768px) {
  .build-footer__text {
    padding-left: 100px;
    padding-right: 100px;
  }
}
@media (min-width: 1200px) {
  .build-footer {
    padding-bottom: 30px;
  }
  .build-footer__text {
    padding-left: 300px;
    padding-right: 300px;
  }
}
.terms__item {
  display: flex;
  width: 100%;
  margin: 0 auto;
  vertical-align: middle;
}
@media (min-width: 768px) {
  .terms__item {
    width: 90%;
  }
}
@media (min-width: 1200px) {
  .terms__item {
    width: 55%;
  }
}
body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Helvetica", sans-serif;
  font-size: 20px;
  color: #ffffff;
  letter-spacing: 0;
  text-align: center;
  line-height: 21px;
  background-color: #0a0a0a;
  box-sizing: border-box;
}
